@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700;800;900&display=swap);
:root {
  --base-bg-img: linear-gradient(315deg, #bdd4e7 0%, #8693ab 74%);
  --base-darker: rgb(51, 51, 51);
  --base-dark: #333333;
  --base-white: #fff;
}

* {
  font-family: Montserrat;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body, .app {
  width: 100vw;
  height: 100%;
  background-color: #045de9;
  background-image: linear-gradient(315deg, #045de9 0%, #09c6f9 74%);
  background-attachment: fixed;
}
.app {
  padding: 6.5rem 1rem 0 1rem;
}
.main-navbar {
  background-color: #333333;
  background-color: var(--base-dark);
  height: 60px;
  width: 100vw;
  color: white;
  position: fixed;
}
.main-navbar-ul {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  margin: 0;
  height: 100%;
  width: 100%;
  padding: 0;
}
.main-navbar-ul > li {
  list-style: none;
}
.main-navbar-ul > li  a{
  color: #fff;
  color: var(--base-white);
}
* {
  color: #fff;
}
.login,
.register {
  width: 30vw;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
}

/* -- LOGIN -- */

h1 {
  margin-top: 0;
  text-align: center;
  font-weight: 700;
  text-shadow: .5px .5px 3px #525252;
}
form {
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
}
.label {
  position:absolute;
	top: 0px;
	left: 30px;
	font-size:16px;
  pointer-event:none;
	transition: all 0.5s ease-in-out;
}
.form-group {
  width: 100%;
  position:relative;
}
.form-control {
  background-color: transparent;
  border: none;
  border-bottom: 2px solid #fff;
  border-radius: 0;
  width: 100%;
  transition: border-color 0.2s;
  padding-left:30px;
}
.form-group .name {
  background: url(data:image/png;base64,) no-repeat scroll 5px 3px;
  background-size: 15px 15px;
}
.form-group .email {
  background: url(data:image/png;base64,) no-repeat scroll 5px 3px;
  background-size: 15px 15px;
}
.form-group .password {
  background: url(data:image/png;base64,) no-repeat scroll 5px 3px;
  background-size: 15px 15px;
}
.form-control:focus {
  background-color: transparent;
  box-shadow: none;
  border-bottom: 2px solid #fff;
}
.form-group input:focus ~ label,
.form-group input:valid ~ label{
  box-shadow: none;
	top: -12px;
	font-size: 12px;
  background-color: transparent;
}
input[type=submit] {
  width: 100%;
  height: 45px;
  border-radius: 30px;
  background-color: var(--base-dark);
  font-weight: 700;
  margin-top: 1rem;
}
input[type=submit]:hover {
  background-color: #3b4a55;
}
.login > p {
  margin: 3rem 0 0 0;
}
.login > p > a, 
.register > p > a{
  color: var(--base-dark);
}
small {
  color: rgb(108, 108, 108);
}
.avatar {
  width: 35%;
}
.avatar > img {
  max-width: 100%;
  height: auto;
  -webkit-filter: drop-shadow(0 0 0.15rem rgb(97, 97, 97));
          filter: drop-shadow(0 0 0.15rem rgb(97, 97, 97));
}

/* -- REGISTER -- */

.profileme-container {
  width: 100vw;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

/* - SIDEBAR - */

.profileme-aside {
  width: 20%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  grid-gap: 2rem;
  gap: 2rem;
}

/* - LIST INFORMATION - */

.profileme-info {
  width: 100%;
  padding: 0;
  box-sizing: border-box;
}
.profileme-info > li {
  padding: .5rem 1rem;
  list-style: none;
  width: 100%;
  word-wrap: break-word;
  
}
.profileme-info > li:nth-child(1) {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  line-height: 1.5rem;
  grid-gap: .7rem;
  gap: .7rem;
}
.profileme-info > li:nth-child(2) {
  text-align: center;

}

/* - USER AVATAR - */

.profileme-avatar {
  width: 200px;
}
.profileme-avatar > img {
  max-width: 100%;
  height: auto;
  -webkit-filter: drop-shadow(0 0 0.15rem rgb(97, 97, 97));
          filter: drop-shadow(0 0 0.15rem rgb(97, 97, 97));
}

.profileme-buttons {
  margin-top: .5rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  grid-gap: .3rem;
  gap: .3rem;
}
.profileme-buttons > * {
  text-decoration: none;
  color: var(--base-white);
  text-align: center;
  width: 100%;
  padding: .3rem .5rem;
  border-radius: 30px;
  background-color: #2d3942;
  font-weight: 500;
  margin-top: .5rem;
  border: none;
}
.profileme-buttons > *:hover {
  background-color: #3b4a55;
  color: var(--base-white);
}

/* - RIGHT CONTENT - */

.profileme-content {
  padding-left: 5rem;
  width: 70%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  grid-gap: 1rem;
  gap: 1rem;
}
.profileme-name {
  height: 30%;
  width: 100%;
}
.profileme-name > p:nth-child(1) {
  font-size: 2.5rem;
  font-weight: 700;
}
.profileme-name > p:nth-child(2),
.profileme-name > p:nth-child(3) {
  margin: .3rem 0;
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
}

/* - LATEST POSTS - */

.profileme-posts {
  padding: 1.5rem 0 0 0;
  height: 70%;
  width: 100%;
  overflow: scroll;
}
.profileme-posts > ul {
  padding: 0;
  overflow: hidden;
  width: 100%;
}
.profileme-posts > ul > li {
  margin: .5rem 0;
  width: 100%;
  padding: .8rem 1rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
  border-bottom: 1px solid rgba(232, 232, 232, 0.475);

}
.profileme-posts > ul > li > div:last-child {
  width: 130px;
  display: flex;
  justify-content: right;
  align-items: center;
  text-align: center;
  flex-direction: row;
}
.profileme-posts > ul > li > div:last-child > a {
  text-decoration: none;
  color: var(--base-white);
  padding: .3rem 1rem;
  border-radius: 30px;
  background-color: #3C4E5C;
  font-weight: 500;
  border: none;
  width: inherit;
}
.edit-profile-cont {
  width: 100vw;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.edit-profile-cont div:first-child {
  width: 200px;
}
.edit-profile-cont img {
  max-width: 100%;
  height: auto;
  -webkit-filter: drop-shadow(0 0 0.15rem rgb(97, 97, 97));
          filter: drop-shadow(0 0 0.15rem rgb(97, 97, 97));
}
.edit-profile-cont form {
  width: 60%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.edit-profile-cont form .form-group {
  width: 100%;
}
.edit-profile-cont form input {
  color: var(--base-dark);
  width: 100%;
}
.edit-profile-cont form input:focus-visible {
  outline: 0;
}
.edit-profile-cont form input[type="text"],
.edit-profile-cont form input[type="password"],
.edit-profile-cont form input[type="number"] {
  padding: .5rem 1rem;
  border-radius: 10px;
  border: none;
}

.edit-profile-cont form input[type="submit"] {
  grid-column: 1 / 3;
  background-color: #2d3942;
  width: 50%;
  margin: 0 auto;
  color: var(--base-white);
}
.edit-profile-cont form input[type=submit]:hover {
  background-color: #3b4a55;
}
/* - ADD POST - */

.posts-add-container {
  height: 20vh;
  width: 60%;
  margin: 0 auto 0 auto;
}
.posts-add-container form {
  margin-top: 1rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.posts-add-container form div:first-child {
  height: 100%;
  width: 80%;
  display: flex;
  flex-direction: column;
}
.posts-add-container form textarea {
  border: 1px solid var(--base-white);
  height: 100%;
  color: var(--base-dark);
  padding: 1rem;
}
.posts-add-container form textarea:focus {
  outline: 0;
}
.posts-add-container form div:last-child {
  height: 50%;
  width: 20%;
}

.posts-add-container form input[type="submit"] {
  border: none;
  padding: .3rem 1.2rem;
  border-radius: 1px;
  background-color: #303f4b;
  color: var(--base-white);
}
.posts-add-container form input[type="submit"]:hover {
  background-color: #586670;
}

/* - POSTS LIST - */

.posts-container {
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
/* - AUTHOR - */

.posts-content-header {
  display: flex;
  flex-direction: row;
  justify-content: start;
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
}
.posts-content-header > a {
  text-decoration: none;
  font-weight: 500;
  color: var(--base-dark);

}
.posts-content-header > a > i{
}
#button-tooltip {
  margin-left: .5rem;
}

/* - POST CARD - */

.posts-card {
  box-shadow: rgba(0, 0, 0, 0.386) 5px 5px 25px;
  padding: 1rem;
  width: 70%;
  margin: .5rem auto;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  background-color: var(--base-white);
}

/* - CONTENT - */

.posts-content {
  width: 100%;
  height: 80%;
}
.posts-content > * {
  margin: 1rem 0;
}
.posts-content  .display-text-group  .displayed-text {
  color: var(--base-dark);
  text-decoration: none;
  font-size: 17px;
}

/* DATE, COMMENTS, LIKES */

.posts-info {
  width: 100%;
  height: 20%;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
}
.posts-info  p {
  margin: 0;
  color: var(--base-dark);
}

/* - READ MORE BUTTON - */

.posts-card .posts-info a {
  text-decoration: none;
  padding: .3rem 1.2rem;
  border-radius: 20px;
  background-color: #3C4E5C;
  color: var(--base-white);
}
.posts-card .posts-info a:hover {
  background-color: #586670;
}

.post-container {
  width: 60%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  grid-gap: 3rem;
  gap: 3rem;
  text-shadow: .5px .5px 4px #0000002e;
}

/* - HEADER - */

.post-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
  height: 3rem;
}
.post-header > div:first-child {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: start;
  width: 100%;
  height: 100%;
  grid-gap: 1rem;
  gap: 1rem;
}
.post-header > div:first-child > p {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: start;
  grid-gap: .5rem;
  gap: .5rem;
}

/* - DATE - */

.calendar-img {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  height: 100%;
  grid-gap: .5rem;
  gap: .5rem;
}
.calendar-img > img {
  max-width: 13px;
  height: auto;
}

.post-header > div:last-child {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: end;
  width: 100%;
  height: 100%;
  grid-gap: 1rem;
  gap: 1rem;
  -webkit-filter: drop-shadow(0 0 0.20rem rgba(94, 94, 94, 0.205));
          filter: drop-shadow(0 0 0.20rem rgba(94, 94, 94, 0.205));
}

/* - LIKE - */

.post-header > div:last-child button {
  height: 35px;
  width: 35px;
  padding: .5rem;
  border-radius: 20px;
  background-color: #3C4E5C;
  color: var(--base-white);
  border: none;
}
.post-header > div:last-child button img {
  max-width: 100%;
  height: auto;
  vertical-align: 0;
}
.post-header > div:last-child button:hover {
  background-color: #586670;
}
.post-header > div:first-child p,
.post-header > div:last-child  p {
  margin: 0;
}

/* - AUTHOR - */

.post-header-user {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: start;
  grid-gap: 1rem;
  gap: 1rem;
  text-decoration: none;
  color: var(--base-white);
  font-weight: 500;
}

/* - TEXT CONTENT - */

.post-content {
  padding: 0 2rem;
}

/* - ADD COMMENT - */

.add-comment form {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: auto;
  width: 70%;
  margin: 1.5rem auto;
  align-items: center;
}
.add-comment form textarea{
  width: 90%;
  height: 8rem;
  background-color: rgba(196, 196, 196, 0.51);
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
    -webkit-border-top-left-radius: 5px;
    -webkit-border-top-right-radius: 5px;
    -moz-border-radius-topleft: 5px;
    -moz-border-radius-topright: 5px;
  border: none;
  padding: 1rem;
  color: var(--base-white);
  border-bottom: 3px solid rgb(255, 255, 255);
}
.add-comment form textarea:focus {
  outline: 0;
}
::-webkit-input-placeholder {
  color: var(--base-white);
}
:-ms-input-placeholder {
  color: var(--base-white);
}
::placeholder {
  color: var(--base-white);
}
.add-comment form input[type="submit"] {
  width: 50%;
  border: none;
}
